:root {
  --primary: #14f195;
  --secondary: #14b8f1;
  --background: #1a1a2e;
  --surface: #16213e;
  --text: #e6e6e6;
  --text-muted: #888;
  --border-radius: 8px;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background);
  color: var(--text);
  min-height: 100vh;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-header {
  background-color: var(--surface);
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: var(--box-shadow);
}

.app-header h1 {
  margin: 0;
  background: linear-gradient(45deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.8rem;
}

.app-main {
  flex: 1;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Buttons */
button {
  background: linear-gradient(45deg, var(--primary), var(--secondary));
  border: none;
  color: #fff;
  padding: 0.75rem 1.5rem;
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--box-shadow);
  margin: 0.5rem;
}

button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* Login */
.login-container, .lobby {
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem;
  background: var(--surface);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

/* Status messages */
.status {
  margin-top: 1rem;
  color: var(--text-muted);
}

/* Chessboard */
.chess-game {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

/* Matchmaking */
.matchmaking {
  max-width: 600px;
  width: 100%;
  background: var(--surface);
  border-radius: var(--border-radius);
  padding: 2rem;
  box-shadow: var(--box-shadow);
}

.player-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}

.player-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: var(--border-radius);
  transition: all 0.3s ease;
}

.player-item:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateX(5px);
}

/* Payment Screen */
.payment-screen {
  max-width: 500px;
  width: 100%;
  background: var(--surface);
  border-radius: var(--border-radius);
  padding: 2rem;
  box-shadow: var(--box-shadow);
  text-align: center;
}

.payment-status {
  margin: 2rem 0;
  padding: 1.5rem;
  background: rgba(0, 0, 0, 0.2);
  border-radius: var(--border-radius);
}

.status-item {
  padding: 1rem;
  margin: 0.5rem 0;
  border-radius: var(--border-radius);
  transition: all 0.3s ease;
}

.status-item.paid {
  background: rgba(20, 241, 149, 0.1);
  border-left: 4px solid var(--primary);
}

.waiting {
  color: var(--text-muted);
  font-style: italic;
  margin: 1rem 0;
}

.back-button {
  background: transparent;
  border: 1px solid var(--text-muted);
  margin-top: 1.5rem;
}

.back-button:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* Responsive */
@media (max-width: 768px) {
  .app-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .app-main {
    padding: 1rem;
  }
}